/* Accordion */
.accordion .card {
  margin-bottom: 0;
}
.accordion .card .card-header {
  position: relative;
  background-color: #fff;
  border-bottom: 0;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  text-transform: none;
  font-weight: 700;
  color: #101737;
  flex-basis: 100%;
  order: 1;
}
.accordion .card .collapseparent{
  order: 2;
}
.accordion .card .card-header .nav-link{
  width: 100%;
  display: inline-block;
  font-weight: 700;
  color: #101737;
  padding: .75rem 1.25rem;
}
.accordion .card .collapseparent.show+.card-header .nav-link,
.accordion .card-header[aria-expanded="true"] {
  background-color: #f7f9fc;
  color: #101737;
}

.accordion .card-header .nav-link::after {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: -3px;
  right: 1.25rem;
  width: 10px;
  height: 2px;
  background-color: #848486;
  -webkit-transform-origin: center;
          transform-origin: center;
  transition: 0.3s;
  opacity: 1;
}

.accordion .card-header .nav-link::before {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: -3px;
  right: 1.25rem;
  width: 10px;
  height: 2px;
  background-color: #848486;
  -webkit-transform-origin: center;
          transform-origin: center;
  transition: 0.3s;
  opacity: 1;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.accordion .card .collapseparent.show+.card-header .nav-link:before,
.accordion .card-header .nav-link[aria-expanded="true"]::before {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  opacity: 0;
}
.accordion .card .collapseparent.show+.card-header .nav-link:before,
.accordion .card .collapseparent.show+.card-header .nav-link:after,
.accordion .card-header .nav-link[aria-expanded="true"]::after, 
.accordion .card-header .nav-link[aria-expanded="true"]::before {
  background-color: #101737;
}

.accordion .card-header i {
  position: relative;
  top: 0;
  left: 0;
  margin-right: 10px;
  font-size: 20px;
}
.accordion .card .collapseparent.show+.card-header .nav-link i,
.accordion .card-header[aria-expanded="true"] i {
  color: #519fff;
}

.accordion .card {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.accordion.with-gap .card-header {
  border-radius: 8px 3px;
}

.accordion.with-gap .card {
  margin-bottom: 20px;
}
.accordion.with-gap .card:last-child{
  margin-bottom: 0;
}
.accordion.with-gap .card:first-of-type, .accordion.with-gap .card:not(:first-of-type):not(:last-of-type) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

